<template>
    <div>
      <Empty description=""></Empty>
      <Button type="primary" @click="backToHome"></Button>
      <Button type="primary" @click="backToLast"></Button>
    </div>
</template>
<script>
  import {Button,Empty} from 'element-ui'
  export default {
    name:'404',
    components : {Button,Empty},
    methods:{
      backToHome(){
        this.$router.push('/dashboard');
      },
      backToLast(){
        this.$router.go(-1);
      }
    }
  }
</script>
<style scoped>
  div {
    text-align : center;
  }
</style>
